import axios from 'axios';

const submit = (
  selectedOblast,
  companyName,
  billingQuantity,
  position,
  phone,
  email,
) => {
  const region = selectedOblast.value;
  const accounts = billingQuantity.value;
  const enterprise = companyName;
  return axios.post('/get-price.php', {
    region,
    enterprise,
    accounts,
    position,
    phone,
    email,
  });
};

export default submit;
