import React from 'react';
import Media from 'react-media';
import { Link } from 'react-router-dom';
import style from './footer.module.css';
import ContactsInfo from './contactsInfo/contactsInfo';
import logo from '../../../img/logo/LogoCCT.png';
import logoW from '../../../img/logo/LogoCCT.webp';

const Footer = () => (
  <section className={style.section}>
    <Media query={{ maxWidth: 1199 }}>
      {matches =>
        matches && (
          <Link className={style.link} to="/">
            <picture>
              <source srcSet={logoW} type="image/webp" />
              <source srcSet={logo} type="image/png" />
              <img className={style.logo} src={logo} alt="Logo" />
            </picture>
          </Link>
        )
      }
    </Media>
    <div className={style.wrapper}>
      <ContactsInfo />
    </div>
    <div className={style.footer}>
      (с) 2019 Cyber Creation Team - автоматизация коммунальных предприятий
    </div>
  </section>
);

export default Footer;
